import React from "react";
import { NavLink } from "react-router-dom";
import MemberComponent from "components/MemberComponent";

const AboutUsPage: React.FC = () => {
  return (
    <div>
      {/* Header Section */}
      <header className="bg-blue-600 text-white text-center px-4 py-20">
        <div className="container mx-auto">
          <h1 className="text-4xl font-bold mb-4">Tentang Kami</h1>
          <p className="text-xl mb-8">
            Ketahui lebih lanjut tentang perusahaan kami dan misi kami.
          </p>
        </div>
      </header>

      {/* Company Story Section */}
      <main>
        <section id="company-story" className="py-20 bg-gray-100 px-4">
          <div className="container mx-auto text-center">
            <h2 className="text-3xl font-bold mb-8">Kisah Kami</h2>
            <p className="text-lg mb-8">
              Kami dimulai dengan misi untuk menyederhanakan pembayaran kasir
              untuk bisnis di Indonesia. Perjalanan kami dimulai dengan tim
              kecil individu yang bersemangat dan percaya pada kekuatan
              teknologi untuk mengubah cara pembayaran diproses. Hari ini, kami
              bangga melayani ribuan bisnis, membantu mereka menyederhanakan
              operasi mereka dan meningkatkan pendapatan mereka.
            </p>
          </div>
        </section>

        {/* Team Section */}
        <section id="members" className="py-20 px-4">
          <div className="container mx-auto text-center">
            <h2 className="text-3xl font-bold mb-8">Tim Kami</h2>
            <div className="flex flex-wrap justify-center">
              <MemberComponent
                name="John Doe"
                role="CEO"
                imageSrc="assets/team-member1.jpg"
              >
                John adalah visioner di balik perusahaan kami, memimpin kami
                dengan keahlian dalam teknologi dan bisnis.
              </MemberComponent>

              <MemberComponent
                name="Jane Smith"
                role="CTO"
                imageSrc="assets/team-member1.jpg"
              >
                Jane mengawasi strategi teknis dan pengembangan platform kami,
                memastikan kami tetap berada di depan persaingan.
              </MemberComponent>

              <MemberComponent
                name="Emily Brown"
                role="CMO"
                imageSrc="assets/team-member1.jpg"
              >
                Emily memimpin upaya pemasaran kami, menggerakkan strategi
                pertumbuhan dan keterlibatan untuk mencapai lebih banyak bisnis.
              </MemberComponent>
            </div>
          </div>
        </section>
      </main>

      {/* Call to Action Section */}
      <footer className="bg-blue-600 text-white text-center py-20 px-4">
        <div className="container mx-auto">
          <h2 className="text-3xl font-bold mb-4">
            Bergabung dengan Perjalanan Kami
          </h2>
          <p className="text-xl mb-8">
            Kami selalu mencari individu berbakat untuk bergabung dengan tim
            kami dan membantu kami mencapai misi kami.
          </p>
          <NavLink
            to="/career"
            className="bg-white text-blue-600 font-semibold py-2 px-4 rounded"
          >
            Lihat Karir
          </NavLink>
        </div>
      </footer>
    </div>
  );
};

export default AboutUsPage;
